import React, {useEffect} from 'react';
import {ErrorMessage, Field, Form, Formik} from 'formik';
import s from './Login.module.css'
import {Navigate, useNavigate} from "react-router-dom";
import {AuthMeDataType, setAuthMeDataAC, signInThunk} from "../../state/reducers/authMe-reducer";
import {useAppDispatch, useAppSelector} from "../../state/hooks";
import {ProfilePageType} from "../../state/reducers/profile-reducer";

type ProfilePropsType = {
    profileUser: ProfilePageType
}

export const SignIn = ({profileUser}: ProfilePropsType) => {
    const navigate = useNavigate();
    const dispatch = useAppDispatch()
    const initializeStatus = useAppSelector(state => state.initialize.initializeProgress)
    const error = useAppSelector<string | undefined>(state => state.authMe.me.error)
    const isLoggedUser = useAppSelector(state => state.authMe.me)
    useEffect(() => {
        const logoutData: AuthMeDataType = {
            id: 0,
            email: '',
            login: '',
            error: ''
        }
        dispatch(setAuthMeDataAC(logoutData))
    },[])

    if(isLoggedUser.login != ''){
        return <Navigate to={'/'}/>
    }
    return (
        <div className={s.SignIn}>
            <h1>Sign In</h1>
            <Formik
                initialValues={{email: '', password: '', rememberMe: false}}
                validate={values => {
                    const errors = {};
                    if (!values.email) {
                        // @ts-ignore
                        errors.email = 'Required';
                    } else if (
                        !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email)
                    ) {
                        // @ts-ignore
                        errors.email = 'Invalid email address';
                    }
                    return errors;
                }}
                onSubmit={(values, {setSubmitting}) => {
                    dispatch(signInThunk(values.email, values.password, values.rememberMe))
                    setTimeout(() => {
                        if (initializeStatus === false) {
                            if (error != "") {
                                setSubmitting(false)
                            } else {
                                setSubmitting(true)
                            }
                        }
                    }, 1000)
                }}
            >
                {({isSubmitting}) => (
                    <Form className={s.form}>
                        <div>
                            <Field type="email" name="email"/>
                            <ErrorMessage name="email" component="div"/>
                        </div>
                        <div>
                            <Field type="password" name="password"/>
                            <ErrorMessage name="password" component="div"/>
                        </div>
                        <div>
                            <Field type="checkbox" name="rememberMe"/> Remember Me
                            <ErrorMessage name="rememberMe" component="div"/>
                        </div>
                       <div> {error === '' ? "" : error}</div>
                        <button type="submit" disabled={isSubmitting}>
                            Submit
                        </button>
                    </Form>
                )}
            </Formik>
        </div>
    );
}

